.post {
  width: 385px;
  height: 480px;
  margin-top: 10px;
  margin-right: 25px;
  margin-bottom: 40px;
  margin-left: 25px;
  padding-bottom: 5px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.post::after {
  content: "";
  border-radius: 15px;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.post:hover {
  opacity: 1;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  transform: scale(0.98, 0.99);
}

.post:hover::after {
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.postImg {
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-radius: 8px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  color: #be9656;
  line-height: 20px;
  margin-top: 10px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  font-weight: 700;
  cursor: pointer;
}

.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  color: #999;
  margin-top: 5px;
}

.postDesc {
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  padding: 5px 25px;
}

