@charset "utf-8";
body {
  margin: 0;
  padding: 0px;
  background-color: #fff;
}

.cleanup {
  min-height: 100vh;
}

.socialIcon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(228, 224, 224, 0.3);
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.1);
}

.Icon {
  background-color: rgba(228, 224, 224, 0.3);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  padding: 20px;
  color: #7b7c7c;
  font-size: 1.1rem;
}

#contact {
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
}

.contact-box {
  background-color: #434445;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  box-shadow: 2px 0 30px rgba(0, 0, 0, 0.1);
}

.c-heading h1 {
  font-family: Roboto;
  color: #e7eef3;
  font-size: 2.5rem;
  font-weight: ligher;
  margin: 0;
  padding: 0;
}

.c-heading p {
  font-family: Roboto;
  color: #cecece;
  font-size: 0.8rem;
  font-weight: ligher;
  margin: 0;
  padding: 0;
  text-align: center;
}

.c-inputs {
  margin: 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.c-inputs input {
  width: 250px;
  height: 45px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgba(251, 251, 251, 0.1);
  outline: none;
  margin: 10px 0;
  padding: 10px;
  block-size: border-box;
  color: #f4f4f4;
  display: flex;
}

.c-inputs textarea {
  width: 250px !important;
  height: 200px !important;
  outline: none;
  background-color: rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(82, 82, 82, 1);
  color: #f4f4f4;
  padding: 10px;
  font-size: 1.2rem;
  box-sizing: border-box;
}

.c-inputs button {
  width: 200px;
  height: 40px;
  background-color: #fff;
  border: none;
  outline: none;
  margin-top: 20px;
  border-radius: 10px;
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.1);
  letter-spacing: 1px;
}

.c-inputs input:focus {
  border-bottom: 1px solid rgba(252, 252, 252, 1);
  transition: all ease 0.5s;
}

.c-inputs textarea:focus {
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 1);
  transition: all ease 0.5s;
}

.c-inputs button:active {
  transform: scale(1.02);
}
.Icon:hover {
  color: #000;
  transition: all ease 0.3s;
}

.map {
  filter: grayscale(0.2);
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.2);
  border: 2px solid #fff;
}

@media (max-width: 1100px) {
  .map {
    display: none;
  }
  #contact {
    margin-top: 30vh;
  }
  .cleanup {
    height: 130vh;
  }
}

@media (max-width: 380px) {
  .socialIcon {
    display: none;
  }

  .contact-box {
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
  }

  #contact {
    position: static;
    transform: translate(0, 0);
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
  }

  .c-heading h1 {
    font-size: 2.2rem;
    text-align: center;
  }
}

